@import url(https://fonts.googleapis.com/css2?family=Rowdies:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Quicksand', sans-serif;
  background-color: #fcfcfc;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

h1, h2, h3, h4, details > summary {
  font-family: 'Rowdies', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
}

h1 {
  font-size: 48px;
}

h4 {
  font-size: 24px;
}

p ul a {
  font-size: 18px;
}

button {
  background-color: #18A0FB; /* blue */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  box-sizing: content-box;
  border-radius: 5px !important;
}
.nav-elements {
  background-image: url(/static/media/header.76c92331.svg);
  background-repeat: no-repeat;
  background-size: 100%;
  padding-top: 50px;
}

.logo {
  width: 15%;
  text-align: center;
  display: inline-block;
  text-decoration: none;
}

.nav-items {
  margin-left: -28px;
  margin-top: 40px;
  height: 80px;
}
.menu {
    list-style: none;
    text-align: center;
}

.menu li {
    display: inline-block;
    position: relative;
    width: 200px;
    padding: 10px;
    text-decoration: none;
    background-color: #fcfcfc;
    text-align: center;
}

.menu li > a {
    color: black;
    text-decoration: none;
    font-weight: bold !important;
    font-size: 20px;
}

li > a:hover {
    background-color: gray;
    color: black;
}

.menu li:hover {
    background-color: gray;
    text-decoration: none;
}

/*--- Sublist Styles ---*/
.menu ul {
    position: absolute;
    right: 0;
    left: auto;
    z-index: 9999;
    display: none
}

/*--- Hide Sub Sublists ---*/
.menu li:hover ul ul {
    display: none;
}

/*--- Sublevel UL's display and position on hover ---*/
.menu li:hover ul {
    display: block;
}
.menu li li:hover ul {
    position: absolute;
    left: 168px;
    top: 0px;
    display: block;
}

